<template>
  <div class="cjset">
    <p class="title"></p>
    <div class="btn_wrap">
      <p>
        <van-button type="primary" v-on:click="cj"></van-button>
        <span class="fr" v-if="viewCjTime!=''">{{viewCjTime}}</span>
      </p>
      <p>
        <van-button type="primary"  v-on:click="sb"></van-button>
        <span class="fr" v-if="viewSbTime!=''">{{viewSbTime}}</span>
      </p>
      <p><van-button type="primary"></van-button></p>
    </div>
    <div>
      <van-actionsheet
        v-model="show"
        :actions="mod"
        @select="oncj"
      />
      <van-actionsheet
        v-model="showTow"
        :actions="modTow"
        @select="onsb"
      />
    </div>

    <!--  -->
    <div class="dialog">
      <van-dialog
        v-model="showdialog"
        show-cancel-button
        :before-close="beforeClose"
        :title="''"
        @confirm="onConfirm"
      >
        <van-field
          v-model="cjtime"
          label=""
          type="number"
          placeholder="1-60"
          oninput="if(value.length>2)value=value.slice(0,2)"
        />
      </van-dialog>
    </div>

    <!--  -->
    <div class="dialog">
      <van-dialog
        v-model="showdialogTow"
        show-cancel-button
        :before-close="beforeCloseTow"
        :title="''"
        @confirm="onConfirm"
      >
        <van-field
          v-model="sbTimeM"
          label=""
          type="number"
          placeholder="10-60"
          oninput="if(value.length>2)value=value.slice(0,2)"
        />
      </van-dialog>
    </div>

    <!--  -->
    <div class="dialog">
      <van-dialog
        v-model="showdialogThree"
        show-cancel-button
        :before-close="beforeCloseThree"
        :title="''"
        @confirm="onConfirm"
      >
        <van-field
          v-model="sbTimeH"
          label=""
          type="number"
          placeholder="1-24"
          oninput="if(value.length>2)value=value.slice(0,2)"
        />
      </van-dialog>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { Button,Actionsheet,Dialog ,Toast } from 'vant';

export default {
  name: 'cjset',
  components: {
    Button,
    Actionsheet,
    Dialog ,
    Toast 
  },
  methods:{
    cj(){
      this.show=true
    },
    oncj(item){
      this.show = false;
      if(item.name==''){
        this.custom()
      }else {
        this.viewCjTime=item.name
        this.viewCjTimeVal=item.val
      }
      console.log(item)
    },
    custom(){
      this.showdialog=true
    },
    beforeClose(action, done){
      if (action === 'confirm') {
        let reg=/0\d/
        let cjtime=this.cjtime.replace(/^\s+|\s+$/gm,'')
        if(this.cjtime=='' || this.cjtime=='00' ||reg.test(cjtime)){
          Toast.fail('');
          done(false)
          return
        }
        this.viewCjTime=this.cjtime+''
        this.cjtime=''
        setTimeout(done, 1000);
      } else {
        done();
      }
    },
    onConfirm(){

    },


    sb(){
      this.showTow=true
    },

    onsb(item){
      this.showTow = false;
      if(item.name==''){
        this.showdialogTow=true
      }else if(item.name=='') {
        this.showdialogThree=true
      }else {
        this.viewSbTime=item.name
        this.viewSbTimeVal=item.val
      }
    },
    customTow(){

    },
    beforeCloseTow(action,done){
      if (action === 'confirm') {
        let reg=/0\d/
        let sbTimeM=this.sbTimeM.replace(/^\s+|\s+$/gm,'')
        if(this.sbTimeM=='' || this.sbTimeM=='00' ||reg.test(sbTimeM) || (sbTimeM>60)){
          Toast.fail('');
          done(false)
          return
        }
        this.viewSbTime=this.sbTimeM+''
        this.sbTimeM=''
        setTimeout(done, 1000);
      } else {
        done();
      }
    },
    beforeCloseThree(action,done){
      if (action === 'confirm') {
        let reg=/0\d/
        let sbTimeH=this.sbTimeH.replace(/^\s+|\s+$/gm,'')
        if(this.sbTimeH=='' || this.sbTimeH=='00' ||reg.test(sbTimeH) || (sbTimeH>24)){
          Toast.fail('');
          done(false)
          return
        }
        this.viewSbTime=this.sbTimeH+''
        this.sbTimeH=''
        setTimeout(done, 1000);
      } else {
        done();
      }
    }
  },
  data(){
    return {
      show:false,
      showTow:false,
      showdialog:false,
      showdialogTow:false,
      showdialogThree:false,
      cjtime:'', // 
      viewCjTime:'', // view 
      viewCjTimeVal:'',// 

      sbTimeM:'',
      sbTimeH:'',
      viewSbTime:'',
    
      modTow:[
        {
          name:'10',
          val:600
        },
        {
          name:'30',
          val:1800
        },
        {
          name:'1',
          className:'aa',
          val:3600
        },
        {
          name:'12',
          val:43200
        },
        {
          name:''
        },
         {
          name:''
        }
      ],
      mod:[
        {
          name:'1',
          val:1
        },
        {
          name:'5',
          val:5
        },
        {
          name:'10',
          className:'aa',
          val:10
        },
        {
          name:''
        }
      ]
    }
  },
  mounted(){
    // console.log(this.params,'-----------params')
  }
}
</script>

